define({
	//
	toInt: function(str){
	    return parseInt(str, 10) || 0;
	},
	/**
	 * 
	 * @param  {int} num    
	 * @param  {int} digits 
	 * @return {string}     
	 */
	zeroFill: function(num, digits) {
		var num = '' + num; //"+"
		while(num.length < digits) {
			num = '0' + num;
		}
		return num;
	}
})


